.productItem {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  margin: 10px;
}
.productItem_img {
  max-width: 370px;
  max-height: 370px;
  object-fit: contain;
}
.productItem_title{
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.productItem_price {
  display: flex;
  align-items: center;
}
.productItem_price div, button {
  flex: 1 1;
  justify-content: center;
  height: 100%;
  font-size: 1.125rem;
  font-weight: 500;
}
.productItem_price > button {
  background-color: #0eb1fd;
  border: 0;
  outline: 0;
  cursor: pointer;
  font-weight: 500;
  padding: 5px;
  font-size: 1rem;
}
.productItem_price > button:hover {
  outline: 1px solid gray;
}

/* products modal styles */
.modal_slide {
  position: relative;
  display: flex;
  height: 100%;
}
.modal_btn {
  position: absolute;
  top: 2rem;
  right: 1rem;
  height: 25px;
  width: 25px;
  z-index: 99;
  font-size: 16px;
  font-weight: bold;
}
.modal_products {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .modal_products {
    flex-wrap: wrap;
  }
}
.modal_products img {
  max-width: 30rem;
  max-height: 82vh;
  object-fit: contain;
  margin: auto auto;
}
@media (max-width: 425px) {
  .modal_products img {
    width: 90%;
    margin: 10px;
  }
}
.modal_products_info {
  margin: 1rem;
}
.modal_products_info p {
  margin-bottom: 1rem;
}

.products {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: .5rem;
}
.home_container {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  height: 100%;
  width: 100%;
}
.home_main {
  flex-grow: 1;
  display: flex;
  flex-wrap: wrap-reverse;
}
.home_main_products {
  flex: 3 1 600px;
}
.home_main_sidebar {
  flex: 1 1 200px;
}


.filter {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 10px;
  margin: 10px;
  border-bottom: 1px solid #ddd;
}
.filter div {
  flex: 1 1 150px;
}
@media (max-width:425px) {
  .filter div {
    margin-top: 10px;
  }
}
.checkoutForm {
  width: 90%;
  padding: 5%;
}
.checkoutForm > form {
  padding: 0;
  width: 100%;
}
.form_control {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}


.form_control > label {
  display: block;
  font-weight: 500;
  margin-bottom: 5px;
}
.form_control > input {
  display: block;
  padding: 8px 5px;
}
.form_control > button {
  background-color: #0eb1fd;
  font-size: 1rem;
  border: 0;
  padding: 8px 0;
} 
.cart_header {
  margin: 10px;
  padding: 11.5px 10px;
  border-bottom: 1px solid #ddd;
}
.cart_total {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  margin: 10px;
  padding: 10px;
}
.cart_total p {
  flex: 2 1;
  text-align: center;
  font-weight: 500;
  font-size: 1.25rem;
}
.cart_total button {
  flex: 3 1;
  background-color: #0eb1fd;
  font-size: 1rem;
  outline: 0;
  border: 0;
  font-weight: 500;
  transition: 0.5s;
}
.cart_total button:hover {
  border: 1px solid gray;
}

/* order modal  */
.order_modal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #222;
  padding-top: 10px;
}
@media (max-width: 425px) {
  .order_modal {
    font-size: 80%;
  }
}
.order_modal h3 {
  font-size: 1.25rem;
  color: blueviolet;
  margin: 0;
}
.order_modal > h2 {
  margin: 0.5rem;
}

@media (max-width: 420px) {
  .order_modal h2 {
    font-size: 1.125rem;
    margin-top: 0;
  }
  .order_modal h3 {
    font-size: 1rem;
  }
}

.order_details div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.order_cart {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.order_cart span {
  margin-right: 10px;
}
.order_cart div {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
}
.order_cart_item {
  border-bottom: 1px solid #ddd;
}

.cartItem {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  margin: 10px;
}
.cartItem_img {
  width: 60px;
  margin-right: 5px;
}
.cartItem_content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cartItem_control {
  display: flex;
  align-items: center;
  justify-content: space-between;
  float: right;
}
.cartItem_control > button {
  height: 40px;
  width: 200px;
  font-weight: 400;
}
.cartItem_control > p {
  flex: 1 1;
  text-align: center;
}
.home_header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0.25rem 2rem;
  background-color: rgba(238, 238, 238, 0.5);
  color: #000;
  border-bottom: 1px solid #ddd;
}
@media (max-width: 425px) {
  .home_header {
    justify-content: center;
    padding: 0.5rem;
    text-align: center;
  }
}
.nav_section {
  display: flex;
  align-items: center;
}

.nav_section span {
  margin-left: 1.2rem;
  font-weight: 500;
}
.login_nav{
  display: flex;
  flex-direction: column;
}
.login_nav > p {
  font-weight: 500;
}
.login_nav > small {
  font-size: 12px;
}

.footer {
  height: 50px;
  background-color: #2c3e50;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
}
.login {
  background-color: #eee;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  margin-top: 0;
}
.login_logo {
  display: block;
  padding: 1rem;
  margin: 0 auto;
}

.login_area {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: .5rem;
}
.login_area > form {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 0.2rem;
  box-shadow: -5px 10px 8px rgba(0, 0, 0, 0.08);
  padding: 2.25rem 2rem;
}
.login_area > form > h3 {
  text-align: center;
  margin: 0;
  margin-bottom: 1rem;
}
.form_div {
  margin-bottom: 0.5rem;
}
.form_div label{
  display: block;
  font-weight: 500;
  margin-bottom: 5px;
}
.form_div input {
  display: block;
  padding: 0.4rem 0.5rem;
  border: 1px solid #ccc;
  outline: 0;
  border-radius: 5px;
  font-family: sans-serif;
}
.input_error {
  font-size: .8rem;
  color: red;
}
.login_btn {
  display: block;
  width: 100%;
  background-color: #fe055b;
  color: #fff;
  border: 0;
  outline: 0;
  padding: .3rem;
  margin-top: .8rem;
  font-weight: 400;
  cursor: pointer;
  transition: border 0.5s;
}
.login_btn:hover {
  outline: 1px solid #aaa;
}
.change_login {
  font-size: 13px;
  text-align: center;
  margin-top: 0.2rem;
}
.change_login_btn {
  color: #531c92;
  font-weight: 600;
  cursor: pointer;
  transition: border 1s;
}
.change_login_btn:hover {
  border-bottom: 1px solid #531c92;
}
.login_or {
  margin-top: 1rem;
  margin-bottom: 1rem;
}
.login_with_google {
  width: 280px;
  border: 1px solid #aaa;
  border-radius: 2rem;
  padding: 0.4rem;
  background-color: #fff;
  box-shadow: -5px 10px 8px rgba(0, 0, 0, 0.08);
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: 0.5s;
}
.login_with_google:hover {
  background-color: rgba(14, 177, 253, 0.5);
  font-weight: 500;
}
.login_with_google > img {
  height: 30px;
  width: 30px;
  object-fit: contain;
}
.login_with_google > span {
  flex: 1 1;
  text-align: center;
}
.error {
  text-align: center;
  color: red;
}
.noMatch {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 1rem;
}
.noMatch h1 {
  color: red;
  margin-bottom: 10px;
  font-size: 3rem;
}

.orders_container {
  background-color: #ddd;
  width: 100%;
  min-height: 100vh;
  height: 100%;
}
.orders {
  max-width: 800px;
  margin: 2rem auto;
  padding: 1rem;
  background-color: #eee;
  min-height: 100vh;
  height: 100%;
}
@media (max-width: 425px) {
  .orders {
    font-size: 80%;
  }
}
@media (max-width: 370px) {
  .orders {
    font-size: 65%;
  }
}
.orders > h3 {
  text-align: center;
  color: #333;
}
.ordersItem {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: 1px solid #222;
}

.ordersItem_header {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.order_id {
  color: #0eb1fd;
}
.order_total {
  color: #222;
}
.orders_list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin: .5rem 0;
}
